[data-modal].is-open { opacity: 1; visibility: visible; z-index: 100; }

[data-modal-container] { position: relative; z-index: 1; vertical-align: middle; display: inline-block; }

[data-modal-close] { position: absolute; background-color: transparent; font-size: 0; right: 32px; top: 24px; }

